.cart-page {

    position: relative;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    // padding: 15%;


    .go-back {

        display: flex;
        border: 2px solid black;
        width: 50%;
        justify-content: center;
        border-radius: 5px;
        cursor: pointer;
        padding: .5%;
        transition: all .3s ease-in-out;

        h1 {
            transition: all .3s ease-in-out;
            font-size: 2.7rem;
        }

        i {
            // color: red;
            transition: all .3s ease-in-out;
            font-size: 3rem;
            margin-right: 5%;

            
        }

        &:hover {
            border-color: red;
            box-shadow: -1px 2px 10px black;

            i {
                transition: all .3s ease-in-out;
                transform: translateX(-160px);
                color: red;
            }

            h1 {
                text-shadow: -1px 2px 10px black;
                color: red;

            }
        }

        

      
    }


        .cart-item {
        
            border: 2px solid black;
            margin: .5%;
            width: 50%;
            display: flex;
            font-size: 1.5rem;
            justify-content: space-between;
            align-items: center;
            
            img {
    
                width: 25%;
    
            }
    
            .remove {
    
                p {
    
                    // border: 1px solid red;
                    width: 30%;
                }
            }
    
            .quantity, .remove, .item-total {
    
                width: 34%;
                border: 1px solid black;
                height: 100%;
                display: flex;
                justify-content: space-evenly;
                align-items: center;
    
                i {
                    cursor: pointer;
                    transition: all .1s ease-in-out;
    
                    &.fa-minus-circle {
                        color: red;
    
                    }
    
                    &:hover {
                        transform: scale(1.3);
    
                        &.fa-plus {
    
                            color: green;
                        }
    
                        &.fa-minus {
    
                            color: red;
                        }
    
                        &.fa-minus-circle {
    
                            color: red;
                        }
                    }
                }
    
            }
    
            
        }
    

   

    .sub-total {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        align-self: flex-end;
        text-align: center;
        bottom: 27%;
        right: 25%;
        width: 16.7%;
        height: 7%;
        font-size: 2rem;
        border: 2px solid #46A049;
        border-radius: 5px;
        transition: all .3s ease-in-out;

        color: #46A049;
        background: white;

        &:hover {

            color: white;
            border-color: white;
            background-color: #46A049;
            box-shadow: -1px 2px 10px black;

        }

    }

    .purchase {
        transition: all .3s ease-in-out;

        color: #46A049;
        border-color: #46A049;
        background: white;
        border-radius: 5px;
        position: absolute;
        bottom: 25%;
        right: 25%;
        width: 10.4%;
        height: 3%;
        font-size: 1.3rem;

        &:hover {

            color: white;
            border-color: white;
            background-color: #46A049;
            box-shadow: -1px 2px 10px black;

        }

        &:focus {
            outline: none;
        }
    }


    @media (max-width: 800px) {

        // border: 1px solid red;
        // background: url('../../img/bottles_of_sauce.jpg') no-repeat;
        .main-pic {
            z-index: 0;
            width: 100%;
            height: 100vh;
        }

        .cart-item-container {

            height: 100%;
            width: 100%;
            opacity: .7;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            position: absolute;
            background: black;
            opacity: .8;

            .cart-item {

                z-index: 1;
                flex-direction: column;
                width: 75%;
                height: 30%;
                margin: 5% 0;
                // border: 2px solid yellow;
                color: white;
                // background-color: gray;
                // opacity: 1;
    
                .remove, .quantity, .item-total {
                    width: 100%;
                    // padding: 2%;
                    border-color: white;

                    img {
                        display: none;
                    }
                }

                .remove {
                    border: 1px solid red;
                    
                    .fa-minus-circle {

                        font-size: 2.5rem;
                    }
                }
    
                
            }

            .go-back {
    
                border-color: red;
                background-color: white;
                margin: 2% 0;
                width: 90%;
                
                i {
                    color: red;
                }
    
                h1 {
                    color: red;
                }
    
                &:hover {
    
                    i {
    
                        transform: translateX(-300px);
                    }
    
                    h1 {
                        transform: translateX(-30px);
        
        
                    }
    
                }
            }
            .sub-total {
    
                position: initial; 
                width: 90%;
                align-self: center;
                justify-content: center;
                margin-top: 2%;
            }
    
           
        }

    }

  
}